import { showFileNames } from "./forms";

const drag = () => {
    const uploadElements = document.querySelectorAll('[name="upload"]');

    // drag *
    // dragend *
    // dragenter - объект вошёл в зону DropArea
    // dragexit *
    // dragleave -  объект вышел из зоны DropArea
    // dragover - объект находится над зоной DropArea
    // dragstar *
    // drop - обект отпустили над DropArea

// * перетаскивание DOM елемента на странице

    const mesBlock = document.createElement('div');
    mesBlock.style.cssText = `text-align: center; 
                        display: block; 
                        border: 2px dashed #E5D1FA; 
                        border-radius: 10px; 
                        color: #E5D1FA;
                        padding: 20px 0 0; 
                        height: 100%;
                        opacity: 1; 
                        font-weight: 600`;
    mesBlock.textContent = 'Перетащите сюда ваши файлы';
  
    ['dragenter', 'dragleave', 'dragover', 'drop'].forEach(eventItem => {
        uploadElements.forEach(input => {
            input.addEventListener(eventItem, prevent);
        });
    });
    
    uploadElements.forEach(input => {
        input.addEventListener('dragenter', () => highlighted(input.closest('.file_upload')));
    });

    uploadElements.forEach(input => {
        input.addEventListener('dragleave', () => unhighlighted(input.closest('.file_upload')));
    })

    uploadElements.forEach(input => {
        input.addEventListener('drop', (e) => {
            unhighlighted(input.closest('.file_upload'));
            input.files = e.dataTransfer.files;
            showFileNames(input);
        });
    });
    
    function viewDropAreaElements(dropAreaElem, visible = true) {
        for (let item of dropAreaElem.children) {
            if (item.tagName !== 'INPUT') {
                item.style.display = visible ?  "block" : "none";
            }
        }
       visible ? mesBlock.remove() : dropAreaElem.prepend(mesBlock);
    }

    function highlighted(dropAreaElem) {
        viewDropAreaElements(dropAreaElem, false);
        dropAreaElem.style.height = '250px';
        dropAreaElem.style.backgroundColor = '#ECF2FF';
        dropAreaElem.style.padding = '10px';
        dropAreaElem.style.transition = '0.3s all';
    }

    function unhighlighted(dropAreaElem) {
        viewDropAreaElements(dropAreaElem);
        dropAreaElem.style.height = '';
        dropAreaElem.style.backgroundColor = '';
        dropAreaElem.style.padding = '';
    }

    function prevent(event) {
        event.preventDefault();
        event.stopPropagation();
    }
};

export default drag;