import modal from "./modules/modal";
import sliders from "./modules/sliders";
import forms from "./modules/forms";
import mask from "./modules/mask";
import showMoreStyles from "./modules/showMoreStyles";
import calc from "./modules/calc";
import filter from "./modules/filter";
import pictureSizes from "./modules/pictureSizes";
import accordion from "./modules/accordion";
import burger from "./modules/burger";
import scroll from "./modules/scroll";
import drag from "./modules/drag";

window.addEventListener('DOMContentLoaded', () => {
    'use strict';
    modal();
    sliders('.main-slider-item', 'vertical', null, null, 5000);
    sliders(".feedback-slider-item", '', ".main-prev-btn", ".main-next-btn", 4000);
    forms();
    mask('[name="phone"]');
    showMoreStyles('.button-styles', '.styles > div > div');
    calc('#size', '#material', '#options', '.promocode', '.calc-price');
    filter('.portfolio-menu', '.portfolio-menu > li', '.portfolio-block', 'active');
    pictureSizes();
    accordion('.accordion-heading', '.accordion-block');
    burger('.burger', '.burger-menu');
    scroll();
    drag();

});
