const filter = (headerSelector, tabsSelector, contentSelector, activeTabClass) => {
    const header = document.querySelector(headerSelector),
        tabs = document.querySelectorAll(tabsSelector),
        content = document.querySelectorAll(contentSelector),
        stubInfoBlock = document.querySelector('.portfolio-no');

    function hideContent() {
        stubInfoBlock.classList.remove('animated', 'fadeIn');
        stubInfoBlock.style.display = 'none';
        
        content.forEach((item) => {
            item.style.display = 'none';
            item.classList.remove('animated', 'fadeIn');
        });
        tabs.forEach((item) => {
            item.classList.remove(activeTabClass);
        });
    };
  
    function showContent(activeContentClass, i = 0) {
        let no_portfolio = content.length;
        content.forEach(item => {
            item.classList.add('animated', 'fadeIn');
            if (item.classList.contains(activeContentClass)) {
                item.style.display = 'block';
                no_portfolio--;
            }
        });
        if (no_portfolio == content.length) {
            stubInfoBlock.classList.add('animated', 'fadeIn');
            stubInfoBlock.style.display = 'block';
        }
        tabs[i].classList.add(activeTabClass);
    };

    hideContent();
    showContent('all');
    
    header.addEventListener('click', (e) => {
        const target = e.target;
        
        if (target && target.closest(tabsSelector)) {
            tabs.forEach((item, i) => {
                
                if (item == target || item == target.parentNode) {
                    let activeContentClass = item.classList[0];
                    hideContent();
                    setTimeout(() => showContent(activeContentClass, i))
                }
            })
        }
    });
};

export default filter;