const sliders = (slideSelector, direction, prev, next, duration) => {
    let slideIndex = 1,
        stopInterval;
    const slides = document.querySelectorAll(slideSelector),
        prevBtn = document.querySelector(prev),
        nextBtn = document.querySelector(next);
    
    function showSlides(n) {
            if ( n < 1) {
                    slideIndex = slides.length;
                }
                if (n > slides.length) {
            slideIndex = 1;
        }
        slides.forEach((item, i) => {
            item.style.display = i == slideIndex-1 ? 'block' : 'none';
            item.classList.add('animated');
            item.parentNode.style.overflow = 'hidden';
            item.style.marginBottom = '3rem';
        });
    }
    
    function changeSlide(n) {
        showSlides(slideIndex += n);
    }

    try {
        prevBtn.addEventListener('click', () => {
            changeSlide(-1);
            slides[slideIndex - 1].classList.remove('slideInLeft');
            slides[slideIndex - 1].classList.add('slideInRight');
        });
    
        nextBtn.addEventListener('click', () => {
            changeSlide(1);
            slides[slideIndex - 1].classList.remove('slideInRight');
            slides[slideIndex - 1].classList.add('slideInLeft');
        })
    } catch(e) {}

    function activateSlideAnimation() {
        if (direction === 'vertical') {
            stopInterval = setInterval(() => {
            document.querySelector(slideSelector).parentNode.style.overflow = 'hidden';
            changeSlide(1);
                slides[slideIndex - 1].classList.add('slideInDown');
            }, duration);
        } else {
            stopInterval = setInterval(() => {
                changeSlide(1);
                slides[slideIndex - 1].classList.remove('slideInRight');
                slides[slideIndex - 1].classList.add('slideInLeft');
            }, duration);
        }
    }

    activateSlideAnimation();

    document.querySelector(slideSelector).parentNode.addEventListener('mouseenter', () => {
        clearInterval(stopInterval);
    });

    document.querySelector(slideSelector).parentNode.addEventListener('mouseleave', () => {
        activateSlideAnimation();
    });

    showSlides(slideIndex);
};

export default sliders;