const pictureSizes = () => {
    const pictureBlock = document.querySelectorAll('.sizes-block');

    function showPic(el) {
        let arr = el.querySelector('img').src.split('.');
        let tmpSrc = arr[0].concat('-1.', arr[1]);
        el.querySelector('img').setAttribute('src', tmpSrc);
        el.querySelectorAll('p:not(.sizes-hit)').forEach(p => {
            p.style.display = 'none';
        })
    }

    function hidePic(el) {
        let arr = el.querySelector('img').src.split('.');
        let tmpSrc = arr[0].slice(0, -2).concat('.', arr[1]);
        el.querySelector('img').setAttribute('src', tmpSrc);
        el.querySelectorAll('p').forEach(p => {
            p.style.display = '';
        })
    }

    pictureBlock.forEach(item => {
        item.addEventListener('mouseover', function() {
            showPic(this);
        });
        item.addEventListener('mouseout', function() {
            hidePic(this);
        })
    });
   
    // function replacePicture(dir = 'out', el)  {
    //     let arr = el.querySelector('img').src.split('.');
    //     let tmpSrc = dir == 'in' ? arr[0].concat('-1.', arr[1]) :
    //         arr[0].slice(0, -2).concat('.', arr[1]);
    //     el.querySelector('img').setAttribute('src', tmpSrc);
    // };

    // function showChildren(el) {
    //     el.querySelectorAll('p').forEach(p => {
    //         p.style.display = '';
    //     })
    // };

    // function hideChildren(el) {
    //     el.querySelectorAll('p:not(.sizes-hit)').forEach(p => {
    //         p.style.display = 'none';
    //     })
    // };

    // pictureBlock.forEach(item => {
    //     item.addEventListener('mouseover', function() {
    //         replacePicture('in', this);
    //         hideChildren(this)
    //     });
    //     item.addEventListener('mouseout', function() {
    //         replacePicture('', this);
    //         showChildren(this);
    //     })
    // });
}

export default pictureSizes;