import { clearDataInForm } from "./forms";

const modal = (state) => {
    let isClick = false;

    function bindModal(triggerOpenModalSelector, 
            modalSelector, 
            closeSelector, 
            timerOpenModal, 
            remove = false) {
        const triggerElem = document.querySelectorAll(triggerOpenModalSelector),
            modal = document.querySelector(modalSelector),
            allModals = document.querySelectorAll('[data-modal]');

        triggerElem.forEach(el => {
            el.addEventListener('click', (e) => {
                if(e.target) {
                    e.preventDefault();
                }
                isClick = true;
                allModals.forEach(item => {
                    item.style.display = 'none'
                });
                openModal(modal, remove, timerOpenModal);
                // el.style.display = remove ? 'none' : '';
            })
        });
        
        modal.addEventListener('click', (e) => {
            if(e.target == modal || e.target.closest(closeSelector)) {
                closeModal(modal, allModals);
            }
        });
            
        document.addEventListener('keydown', (e) => {
            if (e.code == 'Escape' && modal.style.display == 'block') {
                closeModal(modal, allModals);
            }
        });
    };
    
    function openModal(elem, remove, timerOpenModal = 0, disposableBtnSelector = '.fixed-gift') {
        const disposableBtn = document.querySelector(disposableBtnSelector);
        if (timerOpenModal) {
            clearTimeout(timerOpenModal);
        }
        if (remove) {
            disposableBtn.style.display = 'none';
            // disposableBtn.remove();
            //Для совместимости с древними браузерами
            // disposableBtn.parentNode.removeChild(el);
        } 
        elem.style.display = 'block';
        elem.classList.add('animated', 'fadeIn');
        document.body.style.overflow = 'hidden';
    };

    function closeModal(modal, allModals) {
        allModals.forEach(item => {
            item.style.display = 'none';
            // clearCalcData();
        });
        // modal.querySelector('form').reset();
        // modal.querySelector('[name="upload"]').files = '';
        if (modal.querySelector('form')) clearDataInForm(modal.querySelector('form'));

        // modal.style.display = 'none';
        document.body.style.overflow = '';
    }

    function showModalByTime(selector, timer = 3000) {
        let timerId = null;
        timerId = setTimeout(() => {
            const elem = document.querySelector(selector);
            let display;
            document.querySelectorAll('[data-modal]').forEach(item => {
                if(getComputedStyle(item).display !== 'none' ) {
                    display = 'block';
                }
            });      
            if (!display) {
                openModal(elem, false);
            }
        }, timer);
        return timerId;
    };

    function showGiftByScroll(selector) {
        const trigger = document.querySelector(selector);
        window.addEventListener('scroll', () => {
            //для поддержки совместимости с древними браузерами
            const scrollHeight = Math.max(document.documentElement.clientHeight, document.body.clientHeight) - 5;
            if (!isClick && window.pageYOffset + document.documentElement.clientHeight >= scrollHeight) {
                trigger.click();
            }
        })
    }
    
    const timerId = showModalByTime('.popup-consultation', 60000);
    bindModal('.button-design', '.popup-design', '.popup-design .popup-close');
    bindModal('.button-consultation', '.popup-consultation', '.popup-consultation .popup-close', timerId);
    bindModal('.fixed-gift', '.popup-gift', '.popup-gift .popup-close', null, true);
    showGiftByScroll('.fixed-gift');
}

